.slide_wrapper {
    display: block;
    width: 100%;
    position: relative;
    height: 700px;
    background: linear-gradient(to right, #f7f7f7, #919191);
}
.slide_description {
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 20%;
    right: 0;
    top: 20%;
    text-align: center;
    color: #fff;
    text-shadow: 1px 1px 1px #000;
    width: 400px;
    padding-left: 20px;
}
.description_background {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fff;
    width: 450px;
    height: 400px;
    opacity: 0.2;
    border-radius: 20px;
    z-index: 0;
}
.slide_image {
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: -10%;
    right: 0;
    height: 100%;
    width: auto;
}
.description_title {
    font-size: 50px;
}
.description_subtitle {
    font-size: 30px;
}
.catalog_button {
    width: 200px;
    height: 50px;
    border-width: 0;
    background-color: #fff;
    border-radius: 0px;
    font-size: 18px;
    cursor: pointer;
}
.catalog_button:hover {
    background-color: #000;
    color: #fff;
}
@media(max-width:845px) {
    .slide_wrapper {
        height: 500px;
    }
    .slide_description {
        position: absolute;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        top: 10%;
        padding-left: 0px;
    }
}
@media(max-width:600px) {
    .slide_wrapper {
        height: 300px;
    }
    .slide_description {
        position: absolute;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        top: 1%;
        padding-left: 0px;
        max-width: 250px;
    }
    .description_title {
        font-size: 40px;
    }
    .description_subtitle {
        font-size: 20px;
    }
}
@media(max-width:370px) {
    .slide_wrapper {
        height: 300px;
    }
    .slide_description {
        left: 0;
    }
    .description_title {
        font-size: 40px;
    }
    .description_subtitle {
        font-size: 15px;
    }
    .slide_image {
        left: 0%;
    }
}
.swiper-button-next { color: red; background-color: #5c4e4e;width: 100px; height: 100px;}