.category_wrapper {
    width: 300px;
    margin: 70px 30px;
    position: relative;
    display: flex;
    justify-content: center;
}
.opacity_block {
    position: absolute;
    width: 100%;
    height: 100%;
    display: block;
    opacity: 0.5;
    background-color: #fff;
}
.category_image {
    width: 300px;
}
.category_title {
    font-size: 40px;
    text-align: center;
    position: absolute;
    z-index: 1;
    top: 35%;
    margin-left: auto;
    margin-right: auto;
    font-weight: 300;
}
@media(max-width:1100px) {
    .category_wrapper {
        width: 250px;
        margin: 50px 10px;
    }
    .category_image {
        width: 250px;
    }
    .category_title {
        font-size: 35px;
    }
}
@media(max-width:820px) {
    .category_wrapper {
        width: 200px;
    }
    .category_image {
        width: 200px;
    }
    .category_title {
        font-size: 30px;
    }
}
@media(max-width:670px) {
    .category_wrapper {
        width: 150px;
    }
    .category_image {
        width: 150px;
    }
    
    .category_title {
        font-size: 25px;
    }
}
@media(max-width:520px) {
    .category_wrapper {
        width: 250px;
        margin: 20px 10px;
    }
    .category_image {
        width: 250px;
    }
    
    .category_title {
        font-size: 35px;
        top: 45%;
    }
}