.header_wrapper {
    display: block;
    text-decoration: none;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: #fff;
    z-index: 9999;
}
a {
    text-decoration: none;
    color: black;
}
.contacts {
    display: flex;
    align-items: center;
    justify-content: end;
    border-bottom: 1px solid #eaeaea;
    padding-left: 40px;
    padding-right: 40px;
}
.contacts >* {
    padding: 10px;
}
.nav_menu {
    padding-left: 40px;
    padding-right: 40px;
}
.menu {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 80px;
}
.menu_button {
    margin-left: auto;
    margin-right: auto;
    font-size: 22px;
}
.menu_button >*:not(:last-child) {
    padding-right: 25px;
}
.logo {
    display: flex;
    align-items: center;  
    position: absolute;
}
.logo >*:first-child{
    padding-right: 10px;  
}
.logo span{
    font-size: 26px;
    font-weight: 600;
}
.menu_burger {
    position: absolute;
    left: 20px;
    display: none;
    border: none;
    background: transparent;
    width: 40px;
    height: 26px;
    cursor: pointer;
}
.menu_burger::before,
.menu_burger::after {
    content: '';
    left: 0;
    position: absolute;
    display: block;
    width: 100%;
    height: 3px;
    border-radius: 10px;
    background: #000;
}
.menu_burger::before {
    top: 0;
    box-shadow: 0 11px 0 #000;
}
.menu_burger::after {
    bottom: 0;
}
.menu_burgerOpen {
    display: none;
}
.menu_burgerClose {
    display: none;
}
.cart_wrapper {
    border-width: 0;
    background-color: #fff;
}
.cart_btn {
    display: flex;
    align-items: center;
    margin-left: 20px;
}
.cart_btn span {
    font-size: 20px;
    font-weight: 600;
}
.contacts_item {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-left: 10px;
}
.contacts_item >*:first-child {
    margin-right: 5px;
}
.menu_red {
    color: red;
}
@media(max-width: 1450px) {
    .menu_button {
        font-size: 18px;
    }
    .menu_button >*:not(:last-child) {
        padding-right: 15px;
    }
}
@media(max-width: 1220px) {
    .menu_button {
        font-size: 16px;
        font-weight: 600;
    }
    .menu_button {
        margin-right: 0;
    }
    .contacts >* {
        padding: 5px;
    }
    .nav_menu {
        padding: 0 30px;
    }
}
@media(max-width: 1030px) {
    .menu_button {
        font-size: 14px;
        font-weight: 600;
    }
    .menu_button >*:not(:last-child) {
        padding-right: 7px;
    }
}
@media(max-width: 880px) {
    .contacts {
        display: none;
    }
    .menu_button {
        display: none;
    }
    .menu_burgerOpen {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 80px;
        left: 0;
        padding-top: 30px;
        width: 100%;
        background-color: #fff;
        border-top: 1px solid #eaeaea;
        font-size: 20px;
    }
    .menu_burgerClose {
        display: none;
    }
    .menu_burgerOpen >* {
        padding-bottom: 30px;
        padding-left: 30px;
    }
    .menu {
        justify-content: center;
        flex-wrap: wrap;
    }
    .menu_burger {
        display: block;
    }
    .logo span{
        position:static;
        font-size: 38px;
    }
    .logo svg {
        width: 25px;
        height: 25px;
    }
    .cart_wrapper {
        margin-left: auto;
    }
    .nav_menu {
        padding-right: 10px!important;
        padding-left: 10px;
    }
}
@media(max-width: 440px) {
    .logo svg {
        width: 20px;
        height: 20px;
    }
    .logo span{
        position:static;
        font-size: 30px;
    }
    .menu_burger {
        width: 35px;
        height: 26px;
    }
}

