.note_wrapper {
    position: absolute;
    top: 120px;
    right: 10%;
    width: 400px;
    height: auto;
    padding: 20px 40px;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
}
.note_title {
    font-size: 20px;
    border-bottom: 3px solid #000;
    padding-bottom: 10px;
    margin-bottom: 20px;
}
.discription_row {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.note_img {
    width: 100px;
}
.note_name {
    font-size: 18px;
    font-weight: 600;
    margin-left: 15px;
}
.note_price {
    font-size: 20px;
    font-weight: 600;
}
.button_row {
    margin-top: 20px;
}
.button_continue {
    background: transparent;
    border: 0;
    width: 190px;
    height: 50px;
    margin-right: 20px;
    cursor: pointer;
}
.button_continue >*{
    border-bottom: 3px solid #000;
    padding: 5px;
}
.button_cart {
    width: 190px;
    height: 50px;
    padding: 0;
    border-width: 0px;
    background-color: #000;
    cursor: pointer;
}
.button_cart >* {
    color: #fff;
}
.left_item {
    display: flex;
    align-items: center;
}
.background_card {
    position: absolute;
    top: 0;
    display: block;
    width: 100%;
    height: 1000px;
    opacity: 15%;
    background-color: #000;
}
.card_info {
    display: block;
}
.card_info_close{
    display: none;
}