.wrapper {
	position: relative;
	max-width: 1350px;
	padding: 0 50px;
	margin-left: auto;
	margin-right: auto;
	padding-bottom: 100px;
	overflow-x: hidden;
}
.main_wrapper {
	display: flex;
}
.castom_wrapper {
}
.castom_title {
	font-size: 30px;
	margin-bottom: 20px;
}
.model_wrapper {
	display: flex;
}
.model_wrapper > * {
	margin-bottom: 30px;
}
.color_wrapper {
	display: flex;
	max-width: 750px;
	overflow-x: scroll;
}
.color_second_wrapper {
	display: flex;
	max-width: 750px;
	overflow-x: scroll;
}
.color_wrapper::-webkit-scrollbar {
	height: 0;
}
.color_second_wrapper::-webkit-scrollbar {
	height: 0;
}
.model_wrapper::-webkit-scrollbar {
	height: 0;
}
.image_block {
	margin-top: 30px;
	margin-left: auto;
}
.image_block__image {
	position: relative;
}
.main_image {
	width: 550px;
	height: 650px;
}
.detail_image {
	width: 550px;
	height: 650px;
	position: absolute;
	top: 0;
	left: 0;
}
.button_form {
	display: block;
	margin-top: 20px;
	width: 550px;
	height: 100px;
	font-size: 30px;
	background-color: #ffffff;
	color: #000000;
	cursor: pointer;
}
.button_form:hover {
	background-color: #000000;
	color: #ffffff;
}
@media (max-width: 1420px) {
	.main_image {
		width: 450px;
		height: 550px;
	}
	.detail_image {
		width: 450px;
		height: 550px;
	}
	.button_form {
		width: 100%;
	}
	.image_block {
		margin-left: 20px;
		margin-right: auto;
	}
}
@media (max-width: 1300px) {
	.color_wrapper {
		max-width: 650px;
	}
	.color_second_wrapper {
		max-width: 650px;
	}
}
@media (max-width: 1200px) {
	.color_wrapper {
		max-width: 520px;
	}
	.color_second_wrapper {
		max-width: 520px;
	}
}
@media (max-width: 1100px) {
	.main_image {
		width: 350px;
		height: 450px;
	}
	.detail_image {
		width: 350px;
		height: 450px;
	}
}
@media (max-width: 1000px) {
	.main_wrapper {
		flex-direction: column;
	}
	.form_wrapper {
		width: 100%;
	}
	.color_wrapper {
		max-width: 100%;
	}
	.color_second_wrapper {
		max-width: 100%;
	}
	.main_image {
		width: 100%;
		height: auto;
	}
	.detail_image {
		width: 100%;
		height: auto;
	}
	.button_form {
		width: 100%;
	}
	.image_block {
		margin-left: auto;
		margin-right: auto;
	}
}
@media (max-width: 600px) {
	.wrapper {
		overflow-y: hidden;
	}
	.model_wrapper {
		overflow-x: scroll;
	}
	.wrapper {
		max-width: 1350px;
		padding: 0 20px;
		margin-left: auto;
		margin-right: auto;
		padding-bottom: 100px;
	}
	.button_form {
		font-size: 16px;
		height: 50px;
	}
}
.form_close_button {
	position: absolute;
	top: 5px;
	left: 95%;
	background: none;
	border: 0;
	cursor: pointer;
}
.form_main_wrapper {
	position: absolute;
	top: 8%;
	margin-left: auto;
	margin-right: auto;
	left: 0;
	right: 0;
	text-align: center;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: center;
	width: auto;
	height: auto;
	padding: 100px;
	width: 750px;
	background-color: #ffffff;
}
.form_wrapper {
	padding-right: 50px;
}
.form_row {
	display: flex;
	font-size: 20px;
	padding-bottom: 10px;
	width: auto;
}
.form_row:nth-child(2n) {
}
.label {
	width: 180px;
	text-align: left;
}
.input {
	margin-left: auto;
	font-weight: 600;
}
.form_image_block {
	position: relative;
	width: 350px;
}
.form_image_block__image {
	width: 350px;
}
.form_main_image {
	width: 350px;
}
.form_detail_image {
	width: 350px;
	position: absolute;
	top: 0;
	left: 0;
}
.form_button_form {
	background-color: #fff;
	width: 100%;
	height: 50px;
	font-size: 20px;
	cursor: pointer;
	margin-top: 20px;
}
.form_button_form:hover {
	background-color: #000000;
	color: #fff;
}
.form_body {
	display: flex;
	flex-direction: column;
}
label {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	padding-bottom: 10px;
	font-size: 20px;
}
input {
	display: block;
	width: 100%;
	height: 25px;
	margin-top: 5px;
}
.form_back {
	position: absolute;
	top: -100px;
	left: -800px;
	width: 1000%;
	height: 100%;
	background-color: #bbbbbb;
	opacity: 50%;
	padding-bottom: 100px;
}
@media (max-width: 1000px) {
	.form_main_wrapper {
		top: 2%;
		padding: 50px;
		width: 650px;
	}
	.form_image_block {
		position: relative;
		width: 350px;
	}
	.form_image_block__image {
		width: 350px;
	}
	.form_main_image {
		width: 350px;
	}
	.form_wrapper {
		padding-right: 30px;
	}
	label {
		font-size: 18px;
	}
	.label {
		width: 130px;
		font-size: 18px;
	}
	.input {
		font-size: 18px;
	}
}
@media (max-width: 800px) {
	.form_main_wrapper {
		top: 2%;
		padding: 50px;
		width: 550px;
	}
	.form_image_block {
		position: relative;
		width: 300px;
	}
	.form_image_block__image {
		width: 300px;
	}
	.form_main_image {
		width: 300px;
	}
	.form_detail_image {
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
	}
	label {
		font-size: 14px;
	}
	.label {
		font-size: 14px;
		width: 110px;
	}
	.input {
		font-size: 14px;
	}
	.form_close_button {
		left: 93%;
	}
}
@media (max-width: 700px) {
	.form_main_wrapper {
		flex-direction: column-reverse;
		width: 75%;
		margin: 0 30px;
	}
	.form_image_block {
		position: relative;
		width: 100%;
		height: auto;
	}
	.form_image_block__image {
		width: 100%;
		height: auto;
	}
	.form_main_image {
		width: 100%;
		height: auto;
	}
}
@media (max-width: 600px) {
	.form_main_wrapper {
		width: 78%;
		margin: 0 30px;
		padding: 30px;
	}
}
@media (max-width: 550px) {
	.form_main_wrapper {
		width: 75%;
		margin: 0 40px;
		padding: 30px;
	}
	.form_close_button {
		left: 92%;
	}
}
@media (max-width: 510px) {
	.form_main_wrapper {
		width: 75%;
		margin: 0 30px;
